/**
 * Description of control
 *
 * @author:
 */

$.Class.extend("bizagi.rendering.getUser", {}, {
    /*
     * Constructor
     */
    init: function (params) {
        // Call base
        this._super(params);

        var properties = this.properties;
        properties.allowDelegatedUser = this.properties.xpath && this.properties.xpath === "idDelegate";
    },

    /*
     * Template method to implement in each children to customize each control
     */
    renderControl: function () {
        var template = this.renderFactory.getTemplate("render-getUser");

        // Render template
        var html = $.fasttmpl(template, {});
        return html;
    },

    renderReadOnly: function () {
        var result = this._super();
        var html = result;
        if(!bizagi.util.isString(html)){
            html = result[0] && result[0].value ? result[0].value : "";
        }
        
        return html;
    },

    /*
     * Public method to determine if a value is valid or not
     */
    isValid: function (invalidElements) {
        var self = this;

        // Call base
        var bValid = this._super(invalidElements);
        return bValid;
    }

});