/**
 * This file contains all keys that can be change the default functionality
 * of Bizagi, please use it carefully.
 *
 * @author Edward J Morales
 */


/**
 * Enable deprecated features from form editor
 *
 * @type {boolean}
 */
bizagi.override.enableDeprecatedProperties = true;

/**
 * Enable custom interceptor
 *
 * @type {boolean}
 */
bizagi.jqxhrList.intercept = true;