/*
 *   Name: Bizagi Render ComboSelected class
 *   Author: Paola Herrera
 *   Comments:
 *   -   This script will define basic stuff for comboSelected renders
 */

$.Class.extend("bizagi.rendering.comboSelected", {}, {
    /*
    * Constructor
    */
    init: function (params) {
        // Call base
        this._super(params);
        this.leftValues = this.filterData();
        this.rightValues = JSON.parse(JSON.stringify(this.properties.value));
    },
    /*
    * Template method to implement in each children to customize each control
    */
    renderControl: function () {
        var template = this.renderFactory.getTemplate("render-comboSelected");
        var html = $.fasttmpl(template, { dataList: this.leftValues });
        return html;
    },

    orderArraySelected: function (dataList) {
        var arrayOrder = dataList;

        var order = arrayOrder.sort(function (a, b) {
            var nA = a.value === undefined || a.value === null ? "" : a.value.toString().toLowerCase();
            var nB = b.value === undefined || b.value === null ? "" : b.value.toString().toLowerCase();
            if (nA < nB)
                return -1;
            else if (nA > nB)
                return 1;
            return 0;
        });

        return order;
    },

    /*
    * Filter data before set
    */
    filterData: function () {
        var self = this;
        var dataListArray = [];

        if(self.properties.data) {
            $.each(self.properties.data, function (i, data) {
                var el = $.grep(self.properties.value, function (value, e) {
                    return (self.properties.xpath == "Organizations") ? value.id == data.id : value.id === data.id;
                });
    
                if (!el.length) {
                    dataListArray.push(data);
                }
            });
        }

        return dataListArray;
    },

    /*
    * Template method to implement in each children to customize each control
    */
    renderControlReadOnly: function () {
        var template = this.renderFactory.getTemplate("render-comboSelectedReadOnly");
        var html = $.fasttmpl(template, {});
        return html;
    }
});